import { Component, OnInit } from '@angular/core';
import { AuthenticationServiceService } from '../authentication-service.service';
import { TokenStorageServiceService } from '../token-storage-service.service';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {

  hideShow : boolean;
  

  constructor( private loginService:AuthenticationServiceService ,  private sesionStore :TokenStorageServiceService ) { }

  ngOnInit() {

    this.hideShow = false;

    

   // console.log( this.loginService.isUserLoggedIn() );
  }

  toggleNavbar(){

    this.hideShow = !this.hideShow;
  }

  ShowifUserIsLogedin() : boolean {

     if( this.sesionStore.getUsername() != null  ){
        return true;
     }
     
  }


  ShowifUserIsNotLogedin() : boolean {

    if( this.sesionStore.getUsername() == null  ){
       return true;
    }
    
 }



}
