import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class UploadFileService {

  constructor( private http : HttpClient ) { }

  getPhotoName( id : number ): Observable<String>{
   // console.log( id );       http://localhost:8080/getphoto
    return this.http.post<String>("https://blog-angular-spring-boot.herokuapp.com/getphoto", id );

  }

  // : Observable<HttpResponse<userIdName>>
  uploadFile( fd : FormData ): Observable<String>{
// fd :   FileList         http://localhost:8080/upload
    console.log("ok");

   return this.http.post<String>( "https://blog-angular-spring-boot.herokuapp.com/upload", fd );
  }


  deletePhotoByName( photo : String ): Observable<String>{

    //         http://localhost:8080/deletefile
    return this.http.post<String>("https://blog-angular-spring-boot.herokuapp.com/deletefile" , photo );
  }
}
