import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Perdorues } from './perdorues';
import { Observable } from 'rxjs';
import { LoginDetails } from './login-details';
import { registerModelDetails } from './SinginRegisterModel';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationServiceService {

  constructor( private http : HttpClient ) { }



// Observable<HttpResponse<string>>       http://localhost:8080/login
 login( ld :LoginDetails): Observable<string>{

  const httpOptions = {
    headers: new HttpHeaders(
      
      {
      'Content-Type': 'application/json' 
      }
      /**
      <HttpResponse<string>>
      
      {responseType: 'text'}
      */
      )
  };

  return this.http.post<string>( 'https://blog-angular-spring-boot.herokuapp.com/login', ld , httpOptions )  
 }


//       http://localhost:8080/singin 
 singIn( rmd : registerModelDetails): Observable<boolean>{

  const httpOptions = {
    headers: new HttpHeaders(
      
      {
      'Content-Type': 'application/json' 
      })
  };

  return this.http.post<boolean>( 'https://blog-angular-spring-boot.herokuapp.com/singin', rmd , httpOptions )  
 
 }

//       http://localhost:8080/deleteacount
 deleteAcount( email : string ) : Observable<boolean>{

  return this.http.post<boolean>( 'https://blog-angular-spring-boot.herokuapp.com/deleteacount', email  )  ;
 
 }

 

}

