import { Component, OnInit } from '@angular/core';
import{ActivatedRoute} from '@angular/router';
import { LoadUserDataServiceService } from '../load-user-data-service.service';
import { error } from '@angular/compiler/src/util';
import { BlogPost } from '../blog-post';
import { Post } from '../postInterface';
import { postComents } from '../postComents';
import { TokenStorageServiceService } from '../token-storage-service.service';
//import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { UploadFileService } from '../upload-file.service';
import * as DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document';

@Component({
  selector: 'app-read-post',
  templateUrl: './read-post.component.html',
  styleUrls: ['./read-post.component.css']
})
export class ReadPostComponent implements OnInit {

  idPost : number;
  blogPostHolder:BlogPost;
  errorMsg;

  userID : number = 0;
  

allComents:postComents[];

 //public Editor = ClassicEditor;
 public Editor = DecoupledEditor;
InputComentBody : String = "";

pc:postComents;

success :boolean ;

unauthorized :boolean;
emptybody :boolean;

 photoname : String = "no_image.jfif";

 
 
  constructor( private route : ActivatedRoute ,   private loadUserDataServiceService : LoadUserDataServiceService,
    private auth: TokenStorageServiceService ,
     private uploadF : UploadFileService  ) { }


     public onReady( editor ) {
      editor.ui.getEditableElement().parentElement.insertBefore(
        editor.ui.view.toolbar.element,
        editor.ui.getEditableElement()
      );
     
  }



  ngOnInit() {

    this.success = false;

          this.unauthorized = false;
          this.emptybody = false;

    this.blogPostHolder = new BlogPost(  "", "" , "", 0 , 0);

   

    let id = parseInt( this.route.snapshot.paramMap.get("id") );
    this.idPost = id;


    var subscriptionVariable  =  this.loadUserDataServiceService.getPostId( this.idPost );

    subscriptionVariable. subscribe( (data)=>{

      //this.post = data;
      //this.blogPostHolder = data;

      console.log(data);

      this.blogPostHolder = new BlogPost(  data.username , data.title , data.body, data.userId,data.id );
      this.userID = data.userId;

      // marrim foton me ane ti id se perdoruesit 
      this.getPhoto( this.blogPostHolder.userId );

      console.log( this.blogPostHolder );
     
    } , 

    
    (error) =>{

      console.log( this.errorMsg );
    });



    //console.log(this.idPost);

    this.readAllcomentsByPId( this.idPost );

    


  }




readAllcomentsByPId(id : number ){

  this.loadUserDataServiceService.getComentsByPostId( id )
  .subscribe(
    (data)=>{

        this.allComents = data;
       // console.log(this.allComents);
      
    }
  );

}

postComent(){

  
  if( this.InputComentBody != "" && this.auth.getUsername() != null ){

     console.log( this.blogPostHolder );
    

    this.pc={
      id:0,
      comentBody:this.InputComentBody,
      postId: this.blogPostHolder.id,
      username: this.auth.getUsername(),
      comentUserName: this.auth.getName(),
    };

    

    this.loadUserDataServiceService.postComent(this.pc).subscribe( 
      (resp)=>{
        if( resp.body == "success"){
          console.log( "success" );

          this.success = true;

          this.unauthorized = false;
          this.emptybody = false;

          window.location.reload();

        }
        
    }

    );
  }
  else if( this.auth.getUsername() == null ){
    console.log( "unauthorized" );

    this.unauthorized = true;

    this.success =  false;
    this.emptybody = false;
  }
  else{
    console.log( "empty coment body" );
    this.emptybody = true ;

    this.success = false;
    this.success = false;
  }

    window.location.reload();

}



deletComent( username : String , id : number ){
   
// this.auth.getUsername()
  if( username  == this.auth.getUsername() || this.auth.getName()  == this.blogPostHolder.username ){
    
    this.loadUserDataServiceService.deleteComentById(id).subscribe(
          ()=>{},
      (error)=>{
        console.log(error);
      }
    )
  }

  window.location.reload();

}



showHideDeleteComentButton( username : String  ) :boolean{

 // console.log(this.auth.getUsername() , this.blogPostHolder.username , username);
  if( username  == this.auth.getUsername() ){
  
   return true;
  } //this.auth.getUsername() 
  else if( this.auth.getName()  == this.blogPostHolder.username ){
    return true;
  }
  else{
    return false;
  }

}


getPhoto( id : number ){

  console.log(id);

  this.uploadF.getPhotoName( id ).subscribe(
    (data)=>{ 

      console.log( data );
      
       this.photoname = data["answer"];
      
      
    },
    (error)=>{
      console.log( error );
    }
  );
}



}
