import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpEvent, HTTP_INTERCEPTORS, HttpHandler } from '@angular/common/http';
import { Observable } from 'rxjs';
import { TokenStorageServiceService } from './token-storage-service.service';

@Injectable({
  providedIn: 'root'
})
export class HttpInterceptorService implements HttpInterceptor{

  constructor( private tokenStorage : TokenStorageServiceService ) { }

  intercept( req: HttpRequest<any>, next: HttpHandler):
  Observable<HttpEvent<any>> {

    let authRequest = req;
    const token = this.tokenStorage.getToken();

    if( token != null ){
       authRequest = req.clone({ headers: req.headers.set( 'Authorization', 'Bearer ' + token )});

    }

  return next.handle( authRequest );

}
}

export const httpInterceptorProviders = [
  { provide: HTTP_INTERCEPTORS, useClass: HttpInterceptorService, multi: true }
];