import { Component, OnInit } from '@angular/core';
import { AuthenticationServiceService } from '../authentication-service.service';
import { registerModelDetails } from '../SinginRegisterModel';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {


valid: boolean;
validtru : boolean;

  rmd : registerModelDetails = {

    email : "" ,
    mbiemer : "" ,
    emer :"" ,
    pasword : "" ,
    mosha : 0

  };


  constructor( private Authenticate : AuthenticationServiceService) { }

  ngOnInit() {

  

  }

  register(){
    
    this.valid = false;
    console.log( this.rmd );

    this.Authenticate.singIn( this.rmd ).subscribe(
      data =>{
        console.log(data);

        this.validtru = true;

       
      },
      (error)=>{
        this.valid = true;
      }
    );
    

  }
}
