import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'lesText'
})
export class LesTextPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    
    //console.log(value);
    //console.log(value.length);

    let temporal ="";
    if(value.length > 2){
  
    temporal = value. substring(0, 40) ;  //parseInt(args[0],5);
      temporal = temporal + "..." ;

    }
    else{
      temporal = value;
    }
    

    return temporal;
    //return " good job " ;
  }

}
