import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LandingPageComponent } from './landing-page/landing-page.component';
import { AbautComponent } from './abaut/abaut.component';
import { ContactComponent } from './contact/contact.component';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { ServicesComponent } from './services/services.component';
import { ErrorPageComponent } from './error-page/error-page.component';
import { EditPostComponent } from './edit-post/edit-post.component';
import { ReadPostComponent } from './read-post/read-post.component';
import { ListComponent } from './list/list.component';
import { LoginComponentComponent } from './login-component/login-component.component';
import { LogoutComponent } from './logout/logout.component';
import { RegisterComponent } from './register/register.component';
import { RouteGuardService } from './route-guard.service';


const routes: Routes = [
  {path:"", redirectTo:"/home", pathMatch:"full"},
  {path:"home", component: LandingPageComponent },
  {path:"login", component: LoginComponentComponent },
  {path:"logout", component: LogoutComponent },
  {path:"register", component: RegisterComponent },
  
  {path:"abaut", component: AbautComponent },
  {path:'readpost/:id', component:ReadPostComponent},
  {path:"posts", component: ListComponent
  
},

  {path:"contact", component: ContactComponent },
  {path:"profile", component: UserProfileComponent , canActivate:[RouteGuardService]
  /** 
children:[
  {path:'edit/:id', component: EditPostComponent },
  {path:"post/:id", component: ReadPostComponent }
]  */
},

{path:'profile/edit/:id', component: EditPostComponent , canActivate:[RouteGuardService] },
  {path:"services", component: ServicesComponent },
  {path:"**", component: ErrorPageComponent }
  
 

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { 


}
