import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationServiceService } from '../authentication-service.service';
import { LoadUserDataServiceService } from '../load-user-data-service.service';
import { LoginDetails } from '../login-details';
import { TokenStorageServiceService } from '../token-storage-service.service';

@Component({
  selector: 'app-login-component',
  templateUrl: './login-component.component.html',
  styleUrls: ['./login-component.component.css']
})
export class LoginComponentComponent implements OnInit {

  // username = 'rapajglen@gmail.com'
  // password = 'pass'

  invalidLogin :boolean;
  validLogin :boolean;

  ld :LoginDetails = {
    "username": "",
    "password": ""
};
  str : string;
  str1 : string;

  constructor( private router: Router, 
    private authenticationServiceService : AuthenticationServiceService,
     private sesionStore :TokenStorageServiceService  ) { }
    

  ngOnInit() {
  }

 

    checkLoginnew()
    {

            if( this.sesionStore.getUsername() == null )
            {

            // console.log( this.sesionStore.getUsername() );

                  this.authenticationServiceService.login( this.ld )
                  .subscribe(
                    data => {
                      this.str = JSON.stringify(data) ;

                      // console.log( this.str );

                      this.str1 =  this.str.substring(12 , this.str.length-2);
                    // console.log( this.str1 );

                    

                    this.sesionStore.saveToken( this.str1 );

                    // console.log( this.sesionStore.getToken() );

                    this.sesionStore.saveUsername( this.ld.username );

                    this. validLogin = true;
                    this.invalidLogin = false;

                    }
                    ,
                    error => {
                      console.log( error );
                      this. invalidLogin=true;
                      this. validLogin = false;
              
                    }
                    
                  );

                

              }

    }

    

}
