import { Component, OnInit, Input } from '@angular/core';
import { BlogPost } from '../blog-post';
import { LesTextPipe } from '../les-text.pipe';
import { ShowHidePostsUserIdPipe } from '../show-hide-posts-user-id.pipe';

import{Router} from '@angular/router';
import { Post } from '../postInterface';
import { TokenStorageServiceService } from '../token-storage-service.service';
import { UploadFileService } from '../upload-file.service';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit {

@Input()
post : BlogPost ;

 
temp : String;


@Input()
id : number;

photoname : String = "no_image.jfif";

 

  constructor( private router : Router, 
     private lesTextPipe:LesTextPipe , 
     private showHidePostsUserIdPipe:ShowHidePostsUserIdPipe ,
     private uploadF : UploadFileService   ) { }



  ngOnInit() {
    // console.log( this.id ,  this.post );


// do te ktheje nje vektor dhe do marri si input id e perdoruesit dhe id e perdorusesit qe eshte ne poste
 
  this.post.showHidePost = this.showHidePostsUserIdPipe.transform(this.post.userId, [this.id]) ;
    //console.log(this.post.showHidePost[2], this.post.showHidePost[3]);
    
    //console.log(this.post.showHidePost);
    

    this.temp = this.post.body;
    this.post.body = this.lesTextPipe.transform(this.post.body);

    
    this.getPhoto( this.post.userId );
  }



  onSelect(id){
this.router.navigate(['/readpost', id]);
  }


  getPhoto( id : number ){

    //console.log( id );

    this.uploadF.getPhotoName( id ).subscribe(
      (data)=>{ 

       // console.log( data );
        
         this.photoname = data["answer"];
        
        
      },
      (error)=>{
        console.log( error );
      }
    );
  }


}
