import { Component, OnInit, ViewChild } from '@angular/core';
import{ActivatedRoute, Router} from '@angular/router';
import { LoadUserDataServiceService } from '../load-user-data-service.service';
import { BlogPost } from '../blog-post';
//import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { Post } from '../postInterface';
import * as DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document';


@Component({
  selector: 'app-edit-post',
  templateUrl: './edit-post.component.html',
  styleUrls: ['./edit-post.component.css']
})
export class EditPostComponent implements OnInit {

  idPost :number;

  //post: BlogPost;

  // newPost : BlogPost;

  newPostInterface ;

  username: String;
  userId: number;

  errorMsg;
  post :Post ;

  //
  temp : String[] = [];


  InputTitle:String = "" ;
  InputTextBody:String = "" ;

   emptyTitle: boolean = false ;
  emptyBody: boolean = false ;

  // public Editor = ClassicEditor;

  public Editor = DecoupledEditor;

  
  editTitle:String = "" ;
  editTextBody:String = "";

  delPos : boolean = false ;



  constructor( private route : ActivatedRoute ,
     private loadUserDataServiceService: LoadUserDataServiceService,
     private router: Router ) { }


     public onReady( editor ) {
      editor.ui.getEditableElement().parentElement.insertBefore(
        editor.ui.view.toolbar.element,
        editor.ui.getEditableElement()
      );
     
  }

  ngOnInit() {

    let id = parseInt( this.route.snapshot.paramMap.get("id") );
   // shife se mos duhet fshire idPost
    this.idPost = id;
    

    
    
  // get data by id . 

   var subscriptionVariable = this.loadUserDataServiceService.getPostId( this.idPost );
 

      subscriptionVariable .subscribe( (data) =>{
        
         this.newPostInterface = data;

      
      this. editTitle  = this.newPostInterface.title;
      this.editTextBody  = this.newPostInterface.body;

      this.temp[0] = this.newPostInterface.title;
      this.temp[1] = this.newPostInterface.body;

        this.username = this.newPostInterface.username;
        this.userId  = this.newPostInterface.userId;
      
       console.log( this.editTextBody  );
       

    },  
    
    (error)=>{

      this.errorMsg = error ;
    } );
 
 
  
  }



  CancelPostAuth(){

    this.InputTitle = this.temp[0];
    // this.post.title;
    this.InputTextBody = this.temp[1];
      // this.post.body;

   
  }

  updateSuces(){

    
    if( this.InputTitle != "" &&  this.InputTextBody != "" ){

    

    this.post = {
     
      id : this.idPost,
      userId : this.userId,
      username : this.username,
      title : this.InputTitle,
      body : this.InputTextBody
    }


    
 
    // Update post
    var subscriptionVariable = this.loadUserDataServiceService. updatePost(this.post);

     subscriptionVariable.subscribe(  ()=>{
      window.location.reload();
     },
      (error)=>{

      this.errorMsg = error ;

    });


  }
  
 else if( this.InputTextBody != "" && this.InputTitle == "" ){
   
    

   this.post = {
     
    id : this.idPost,
    userId : this.userId,
    username : this.username,
    title : this.editTitle,
    body : this.InputTextBody
  }

  // Update post
  var subscriptionVariable = this.loadUserDataServiceService. updatePost(this.post);

  subscriptionVariable.subscribe( ()=>{
    window.location.reload();
  },
    (error)=>{

  this.errorMsg = error ;

  });
  


  }
  else if( this.InputTextBody == "" && this.InputTitle != "" ){

    this.post = {
     
      id : this.idPost,
      userId : this.userId,
      username : this.username,
      title : this.InputTitle,
      body : this.editTextBody
    }

    // Update post
    var subscriptionVariable = this.loadUserDataServiceService. updatePost(this.post);

     subscriptionVariable.subscribe( ()=>{
      window.location.reload();
     }, 
      (error)=>{

      this.errorMsg = error ;

    });




  }
 
  else{
    this.emptyTitle = true ;
    console.log(" Empty body ");
     this.emptyBody = true ;
    console.log(" Empty  title ");

   
  
  }
  
    // window.location.reload();

  }



  DeletePostAuth(){
    
    // delete

    var subscriptionVariable = this.loadUserDataServiceService.deletePosts( this.idPost );
    subscriptionVariable.subscribe( );

    // window.location.reload();
   // this.router.navigate(['posts']);

   this.delPos = true ;
  }

  goProfile(){
    this.router.navigate(['profile']);
  }

  goPosts(){
    this.router.navigate(['posts']);
  }

}
