import { Component, OnInit, ViewChild, AfterViewChecked } from '@angular/core';
import { BlogPost } from '../blog-post';
import { LoadUserDataServiceService } from '../load-user-data-service.service';
//import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { TokenStorageServiceService } from '../token-storage-service.service';
import { userIdName } from '../userIdName';
import { AuthenticationServiceService } from '../authentication-service.service';

//import { CKEditorComponent } from 'ngx-ckeditor';
import { UploadFileService } from '../upload-file.service';
import * as DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document';


 
@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {

  idperdoruesi:number;
  
  blogPost:BlogPost[];

  makePost : boolean;

  newPost : BlogPost;

  errorMsg;

  username : String;

  userIdName : userIdName;
/** CKEditorComponent */
// @ViewChild('ck-editor') ckeditor : any;

/** 
  public Editor = ClassicEditor;
  public model = {
    editorData: ' <p> CK editor using ! </p> '
};
*/
public Editor = DecoupledEditor;

/** */



  InputTitle:String= "";
  InputTextBody:String = "";

  showpost : boolean ;

  deletemyAcount : boolean ; 
  deleteacount : boolean ;  

  selectedFile : any = [] ;

  photodelete: String = "" ;

  userN : String;
  userNam: String;

  photoname : String = "no_image.jfif" ;

  shownoimage: boolean = true;
 
  
  constructor( private loadUserDataServiceService : LoadUserDataServiceService , private TokenStorage : TokenStorageServiceService,
    private authenticate : AuthenticationServiceService,
    private uploadF : UploadFileService ) { }

/** */
    public onReady( editor ) {
      editor.ui.getEditableElement().parentElement.insertBefore(
        editor.ui.view.toolbar.element,
        editor.ui.getEditableElement()
      );
     
  }
/** */

  ngOnInit() {

    
    this.userN = this.TokenStorage.getName();
    this.userNam = this.TokenStorage.getUsername();


    this.deletemyAcount = false;
    this.deleteacount = false;
// console.log( this.TokenStorage.getToken() , this.TokenStorage.getUsername() );
    
this.showpost = false;
    this.makePost = true;

    this.idperdoruesi = 1;

     this.loadUserDataServiceService.getUserId()
     .subscribe( (resp )=>{

      console.log( resp );
      console.log( resp.body.answer , resp.body.name );

     this.userIdName = {
      answer : resp.body.answer,
      name : resp.body.name
     }

     this.idperdoruesi = this.userIdName.answer ;

     this.getuserposts(   );

      //console.log( this.userIdName  );
      this.getPhoto( this.idperdoruesi );

      this.TokenStorage.saveName( this.userIdName.name );

      

     }, 
     (error)=>{

      this.errorMsg = error ;
    console.log( this.errorMsg );
    } ,
    ()=>{
      console.log( "complete get user id");
    }
     );

     
   
   
  // console.log(this.Editor);
   
  
  //this.getPhoto( this.idperdoruesi );

  }


  getuserposts( ){

   // console.log(this.idperdoruesi   );
    var subscriptionVariable =  this.loadUserDataServiceService.getPostsByUserId( this.idperdoruesi );
    subscriptionVariable.subscribe( (data)=>{

      this.blogPost = data ;
     // console.log(data);
      
    },
    (error)=>{

      this.errorMsg = error ;
    console.log( this.errorMsg )
    },
    ()=>{
      console.log( "complete" )
    } );

    
  }



  writePostAuth(){

    this.InputTitle= "";
    this.InputTextBody = "";
    this.makePost = !this.makePost ;

   // this.makePost = true ;

  }

  CancelPostAuth(){

    this.InputTitle= "";
    this.InputTextBody = "";
    this.makePost = false ;
  }

  MakePost(){

     this.username = this.TokenStorage.getUsername();
  //  this.username = this.userIdName .name;

  if( this.InputTitle != null && this.InputTitle != "" 
   && this.InputTextBody != null && this.InputTextBody != ""){

//console.log( this.newPost );
// this.username
    this.newPost = new BlogPost(  this.userN , this.InputTitle, this.InputTextBody, this.idperdoruesi , 0 );

    var subscriptionVariable =  this.loadUserDataServiceService.postPosts( this.newPost );
    subscriptionVariable.subscribe(  );
    
    console.log(this.newPost);

    this.makePost = !this.makePost ;

  }else{

    console.log(" empty body or title.");
  }

    window.location.reload();

  }

  showposts() {
this.showpost = !this.showpost ;

  }

  deleteAcount(){
    console.log( this.TokenStorage.getUsername()  );
    

    this.authenticate.deleteAcount( this.TokenStorage.getUsername()  ).subscribe(
      ( data )=>{
        if( data == true){

          this.deletemyAcount = data ;
        this.showpost = false ;
        this.TokenStorage.signOut();
        }else{
            this.deleteacount = true ;
        }
        
      }
    );
  }

  onFileSelected( event ){
   // console.log( event );
    // console.log( event.target.files.length );

    let i ;

    for( i=0 ; i< event.target.files.length ; i++ ){

        // nje vektor me file
    this.selectedFile[i] = event.target.files[i];
    }
    
  }

  uploadSelectedFile(){
   // console.log( this.selectedFile.length );

       const fd = new FormData();

       let j;
       for( j=0 ; j< this.selectedFile.length ; j++ ){

        fd.append( "file" , this.selectedFile[j], this.selectedFile[j].name );
    
       }

     this.uploadF.uploadFile( fd ).subscribe(
      (res)=>{
        console.log( res );
      }
    );

    // window.location.reload();
  }

  deletePhoto(){

    this.uploadF.deletePhotoByName( this.photodelete ).subscribe();
    this.photoname  = "no_image.jfif";
  }
  // window.location.reload();

  getPhoto( id : number ){

    this.uploadF.getPhotoName( id ).subscribe(
      (data)=>{ 

        console.log( data );
        
         this.photoname = data["answer"];
         this.shownoimage = false;
        
      },
      (error)=>{
        console.log( error );
      }
    );
  }

}
